.page {
  padding: 30px 19px;
  display: flex;
}
.page .page-body {
  display: flex;
  flex-direction: column;
  font-size: 20px;
}
.page .page-body .maps {
  display: flex;
  min-width: calc(100% * 80% * 50%);
  align-items: center;
  justify-content: space-between;
}
.page .page-body .maps .map-static,
.page .page-body .maps .map-embed {
  padding: 10px;
}
.page .page-body .maps .map-static {
  border: 1px black solid;
}
.page .page-body .maps .map-static img {
  width: 100%;
  height: auto;
}
.page .primary-image {
  padding-bottom: 30px;
}
.page .primary-image .card {
  margin-left: -19px;
  border-radius: 0px 0.25rem 0.25rem 0px;
  box-shadow: 1px 1px 6px #000000;
}
.page .primary-image .card img {
  width: 100%;
  box-shadow: 1px 1px 6px #000000;
  margin-bottom: 15px;
}
.page .primary-image .card .card-text {
  font-size: 20px;
}
