.reviews-wrapper {
  box-shadow: 1px 1px 6px #000000;
}
.reviews-wrapper .reviews .card {
  height: 500px;
  align-content: center;
  justify-content: center;
  font-size: 20px;
  padding: 150px;
  text-align: center;
}
.reviews-wrapper .reviews .carousel-caption {
  color: #212529;
}
.reviews-wrapper .reviews .carousel-caption h3 {
  font-size: 20px;
}
.reviews-wrapper .reviews .carousel-control-next-icon {
  background-color: #212529;
}
.reviews-wrapper .reviews .carousel-indicators li {
  background-color: #212529;
}
