.gallery-wrapper {
  box-shadow: 1px 1px 6px #000000;
  padding: 15px 20px;
}
.gallery-wrapper .gallery .card {
  cursor: zoom-in;
}
.modal {
  z-index: 9000008;
}
.modal .rental-modal .carousel-inner .carousel-item {
  height: 500px;
  background-size: cover;
}
.modal .rental-modal .carousel-inner .carousel-item .carousel-image-wrapper {
  display: flex;
  justify-content: center;
  height: 100%;
}
.modal .rental-modal .carousel-inner .carousel-item .carousel-image-wrapper img {
  max-height: 100%;
  max-width: 100%;
}
