.feature-image {
  display: flex;
  align-items: center;
  height: 600px;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.feature-image .feature-image-title {
  font-size: 80px;
  background-color: rgba(255, 255, 255, 0.4);
  width: 100%;
  text-align: center;
}
.feature-image.explore-main {
  background-image: url("../../assets/explore-main.jpg");
}
.feature-image.pine-cone-main {
  background-image: url("../../assets/pine-cone-main.jpg");
}
.mobile .feature-image {
  background-attachment: scroll;
  height: 350px;
}
.mobile .feature-image .feature-image-title {
  font-size: 26px;
}
