.header-wrapper {
  height: 144px;
  background-color: #7395ae;
  position: fixed;
  box-shadow: 2px 0 4px #000000;
  border: 1px solid black;
  width: 100%;
  z-index: 9000001;
}
.header-wrapper .header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: calc(144px * 2 / 3);
  margin: calc(144px / 6) 0;
  background-color: #63786e;
  border: black 1px solid;
  box-shadow: 2px 0 4px #000000;
}
.header-wrapper .header .header-menu {
  display: flex;
  align-items: center;
}
.header-wrapper .header .header-menu .header-menu-item {
  padding: 0 15px;
  text-align: center;
}
.header-wrapper .header .header-menu .header-menu-item .header-menu-button {
  font-size: 16px;
  padding: 8px;
  border: 1px solid black;
  box-shadow: 1px 3px 3px #000000;
}
.header-wrapper .header .header-menu .header-menu-item .rc-menu-button {
  font-weight: 500;
  min-width: 141px;
}
.header-wrapper .header .header-menu .rc-menu-container {
  margin-top: -65px;
}
.header-wrapper .header .header-menu .rc-menu-container .rc-menu__submenu .rc-menu__item {
  padding: 2px 10px;
}
.header-wrapper .header .header-menu .rc-menu-container .rc-menu__submenu .rc-menu__item .submenu-icon {
  padding-right: 5px;
}
.header-wrapper .header .header-menu .rc-menu-container .rc-menu__submenu .rc-menu__item::after {
  content: none;
}
.header-wrapper .header .header-menu .rc-menu-container .rc-menu__item.subcategory-item {
  padding-left: calc(10px + 10px);
}
.header-wrapper .header .header-menu .hamburger-menu {
  padding-right: 15px;
}
.header-wrapper .header .aremores-header {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}
.header-wrapper .header .aremores-header .aremores-logo {
  padding: 15px;
}
.header-wrapper .header .aremores-header .aremores-logo img {
  height: 120px;
  overflow: visible;
  box-shadow: 1px 1px 6px #000000;
  border: 1px solid black;
  border-radius: 4px;
}
.mobile .header-wrapper {
  height: 100px;
}
.mobile .header-wrapper .header {
  height: calc(100px * 2 / 3);
  margin: calc(100px / 6) 0;
}
.mobile .header-wrapper .header .aremores-header .aremores-logo {
  padding: 10px;
}
.mobile .header-wrapper .header .aremores-header .aremores-logo img {
  height: 85px;
}
.mobile .header-wrapper .header .rc-menu {
  min-width: 8rem;
}
.mobile .header-wrapper .header .rc-menu .rc-menu__item {
  font-size: 14px;
  padding: 2px 5px;
}
